import Router from 'vue-router'
import Vue from 'vue'
import routes from './routes'
import {Toast} from 'vant'

Vue.use(Router)
const router = new Router({
  routes
})

/* 路由开始生命周期 */
router.beforeEach((to, from, next) => {
  // 创建加载器
  Toast.loading({
    message: '正在加载界面...',
    forbidClick: true
  })
  next()

/*  // 如果不存在token 则跳转到登录页
  if (!sessionStorage.token && to.path !== '/login') {
    Toast.error('请先登录')
    next({
      path: '/login'
    })
  } else {
    next()
  } */
})

/* 路由结束生命周期 */
router.afterEach(() => {
  Toast.clear()
  window.scrollTo(0, 0)
})

export default router
